





























import Vue, { PropType } from "vue";

interface Menus {
  tab: string;
  content: string;
}

export default Vue.extend({
  name: "Drink",
  props: {
    drinks: {
      required: true
    }
  },
  data() {
    return {
      selected: ""
    };
  },
  methods: {
    addItem(name: string, price: number): void {
      this.$emit("addDrink", { name, price });
    }
  }
});
