































































import Vue from "vue";

interface Menus {
  tab: string;
  content: string;
}

export default Vue.extend({
  name: "Side",
  data() {
    return {
      selected: []
    };
  },
  methods: {
    addItem(name: string, price: number): void {
      this.$emit("addSide", { name, price });
    }
  }
});
