












































































import Vue from "vue";
import { drinkset } from "../db/drink";

interface Menus {
  tab: string;
  content: string;
}

export default Vue.extend({
  name: "Main",
  data() {
    return {
      mainfood: "",
      sets: drinkset,
      ketchup: "",
      big: "",
      drink: ""
    };
  },
  computed: {
    //ボタンの表示
    finish(): boolean {
      if (this.drink && this.mainfood) return true;
      else return false;
    }
  },
  methods: {
    addItem() {
      const mainfood = this.mainfood;
      let price = 0;

      //メインフードの料金計算
      if (mainfood === "タコライスコンボ") price += 1000;
      else if (mainfood === "タコライス") price += 800;
      else if (mainfood === "温卵タコライス") price += 850;
      else if (mainfood === "チーズタコライス") price += 850;

      //大盛なら＋100円
      if (this.big) price += 100;

      if (mainfood === "タコライスコンボ") {
        const data = {
          price,
          mainfood,
          drink: this.drink,
          ketchup: this.ketchup,
          big: this.big
        };
        this.$emit("addItem", data);
      } else {
        //ドリンクがあるなら
        if (this.drink !== "なし") price += 100;
        const data = {
          price,
          mainfood,
          drink: this.drink,
          ketchup: this.ketchup,
          big: this.big
        };
        this.$emit("addItem", data);
      }
      //フォームをクリア
      this.mainfood = "";
      this.ketchup = "";
      this.big = "";
      this.drink = "";
    }
  }
});
