




























































import Vue from "vue";
import Main from "../components/Main.vue";
import Drink from "../components/Drink.vue";
import Side from "../components/Side.vue";
import Cart from "../components/Cart.vue";

import { drinkData } from "../db/drink";

interface Main {
  price: number;
  mainfood: string;
  drink: string;
  ketchup: string | undefined;
  big: string | undefined;
}
interface SideDrink {
  name: string;
  price: number;
}
interface DrinksFromDB {
  genre: string;
  data: SideDrink[];
}
interface ToCart {
  main: Main[];
  side: SideDrink[];
  drink: SideDrink[];
}
interface Data {
  tab: string;
  menus: string[];
  drinkData: DrinksFromDB[];
  mains: Main[];
  sides: SideDrink[];
  drinks: SideDrink[];
  toCart?: ToCart;
}

export default Vue.extend({
  name: "Home",
  components: { Main, Drink, Side, Cart },
  data(): Data {
    return {
      tab: "tab",
      menus: ["MAIN FOODS", "SIDE FOODS", "DRINK"],
      drinkData: drinkData,
      mains: [],
      sides: [],
      drinks: []
    };
  },
  methods: {
    addMain(main: Main): void {
      this.mains.push(main);
    },
    addSide(side: SideDrink): void {
      this.sides.push(side);
    },
    addDrink(drink: SideDrink): void {
      this.drinks.push(drink);
    },
    catchErr(err: any) {
      alert(`エラーが発生いたしました。
      もう一度ご注文をお願い致します。
      ${err}`);
    }
  }
});
